import axios from 'axios';
import { ajaxDebugger } from '~/util/debug';
import message from 'antd/lib/message';
import 'antd/dist/antd.css';
import { host } from './config';

/**
 * 
 */
const getAjaxId = ( function () {
  let id = 0;
  return () => id++;
})() 

/**
 * 
 * @param {'get' | 'post'} method 
 * @param { string } url 
 * @param { any } data 
 * @param errMsg 
 */
function ajax(method, url, params, errMsg = '') {
  return new Promise((reslove, reject) => {
    let ajaxParams = {};
    let ajaxMethod = {};
    if (method === 'get') {
      ajaxMethod = axios[method];
      ajaxParams = { params };
    } else if (method === 'post') {
      ajaxMethod = axios[method];
      ajaxParams = params;
    } else {
      // 
      ajaxDebugger('');
      return;
    }
    const ajaxId = getAjaxId();
    ajaxDebugger('', `${method}(${ajaxId}) `, url);
    ajaxMethod(url, ajaxParams).then((res) => {
      const body = res.data;
      const { code, data, errMsg } = body;
      ajaxDebugger('', `${method}(${ajaxId}) `, url, code, body);
      if (isGlobalErrCode(code)) {
        ajaxDebugger('glob err ajax');
        //   reject
        message.error(errMsg);
        reslove(body);
      } else {
        //   reslove
        reslove(body);
      }
    }, (err) => {
      //  or 
      ajaxDebugger('', `${method}(${ajaxId}) `, url);
      ajaxDebugger(err);
      message.error(errMsg);
      reject(err);
    });
  });
}

/**
 * 
 * @param {*} code 
 */
function isGlobalErrCode(code) {
  // TODO 
  return (code == 500 ) ? true : false;
}
export { ajax };
export const get = (url, params, errMsg) => {
  return ajax('get', url, params, errMsg)
};

export const post = (url, data, errMsg) => {
  return ajax('post', url, data, errMsg)
};
